body {
	box-sizing: border-box; 
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

main {
	background-color: #61dafb;
	min-height: 89vh;
}
.Tickets {
	margin: 0 auto;
	width: 80%;
}
.support {
	margin-top: 16px;
}

.CheckboxGroup {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.CheckboxGroup .CheckboxItem {
	width: 75%;
	margin: 0 auto;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between; 
}

.CheckboxItem .btnOnly {
	opacity: 0.15;
}

.CheckboxItem:hover .btnOnly {
	opacity: 0.7;	
}

.CheckboxItem .btnOnly:hover {
	opacity: 1;
}
.info {
	min-height: 20vh;
	margin-top: 16px;
	margin-bottom: 16px;
}

.info .nameAirline {
	width: 50%;
}

.info .btnBuyTickets {
	min-width: 60%;
	background-color: #FF6D00;
}

.info .schedule {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	-webkit-align-items: center;
	        align-items: center;
	height: 100%;
}

.info .schedule-info {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

